import React, { useState } from 'react';
import {
  Paper,
  Typography,
  Slider,
  Button,
  Box,
  Divider,
} from '@mui/material';

const TrainingPanel = () => {
  const [learningRate, setLearningRate] = useState(0.01); // Default learning rate

  const handleLearningRateChange = (event, newValue) => {
    setLearningRate(newValue);
  };

  const handleStartTraining = () => {
    alert(`Training started with learning rate: ${learningRate}`);
    // Later, replace this alert with backend integration
  };

  return (
    <Paper
      elevation={6}
      sx={{
        padding: 3,
        borderRadius: 4,
        background: '#1e1e1e',
        color: 'white',
        maxWidth: 600,
        margin: 'auto',
        marginTop: 4,
      }}
    >
      <Typography variant="h4" gutterBottom>
        Train Your AI Model
      </Typography>
      <Box sx={{ marginBottom: 3 }}>
        <Typography variant="subtitle1">Learning Rate</Typography>
        <Slider
          value={learningRate}
          onChange={handleLearningRateChange}
          min={0.0001}
          max={0.1}
          step={0.0001}
          valueLabelDisplay="auto"
          sx={{ color: '#6a11cb' }}
        />
        <Typography>Current Learning Rate: {learningRate.toFixed(4)}</Typography>
      </Box>
      <Divider sx={{ my: 3 }} />
      <Button
        variant="contained"
        color="primary"
        fullWidth
        onClick={handleStartTraining}
      >
        Start Training
      </Button>
    </Paper>
  );
};

export default TrainingPanel;
